class StringSet {

    private stringList: string[];

    constructor() {
        this.stringList = [];
    }

    public add(elem: string): void {
        this.stringList.push(elem);
    }

    public member(elem: string): boolean {
        for (let i: number = 0; i < this.stringList.length; i = i + 1) {
            let locElem = this.stringList[i];
            if (locElem === elem) return true;
        }
        return false;
    }

    public delete(elem: string): void {
        if (this.member(elem)) {
            let locStringList: string[] = [];
            for (let i: number = 0; i < this.stringList.length; i = i + 1) {
                let locElem = this.stringList[i];                
                if (locElem != elem) {
                    locStringList.push(locElem);
                }
            }
            this.stringList = locStringList;            
        }        
    }

    public getToString(): string {

        return this.stringList.toString();      
    }

}

export default StringSet;
