import React, { Component } from "react";

//Demonstracja cyklu życia komponentu

//UWAGA: Dla zobaczenia wyniku działania console.log trzeba 
//użyć Chroma i włączyć w nim "Narzedzia dla developerów" (Ctrl+Shift+I)

interface Props {
};

interface State {
    counter: number;
};

class MyComponentLife extends Component<Props, State>
{
    constructor(props: Props) {
        super(props);

        this.state = {
            counter: 0
        }

        this.increase = this.increase.bind(this);
        
        // etap wygenerowania stanu początkowego komponentu
        
    }

    increase() {                            
        this.setState((prevState: State) => ({
            counter: prevState.counter + 1
        }));        
    }

    static getDerivedStateFromProps(props: Props, state: State) {
        
        // Aktualizacja stanu na skutek zmiany props
        
        console.log("getDerivedStateFromProps - sprawdzenie props");

        return null;
    }
    
    getSnapshotBeforeUpdate(prevProps: Props, prevState: State) {
        
        // Wykorzystywana jest do wykonania operacji przed aktualizacją komponentu

        console.log("getSnapshotBeforeUpdate - PRZED AKTUALIZACJĄ");

        return null;        
    }

  

    componentDidMount(): void {
        
        // Wywoływana, gdy komponent został utworzony, czyli po pierwszym wywołaniu
        // render(). Służy do wywoływania operacji inicjujących, np. za pomocą HTTP

        console.log("componentDidMount - MONTOWANIE");
        
        
    }

    componentDidUpdate(prevProps: Props, prevState: State): void {
        
        // Przykładowe użycie — wykonywanie akcji na podstawie informacji, czy zmienione
        // zostały props (właściwości), np. wysłanie ponownego żądania HTTP, jeśli wykryto,
        // iż komponent otrzymał nową wartość props
        //Wykonywane po aktualizacji komponentu

        
        console.log("componentDidUpdate - PO AKTUALIZACJI");
        
    }

    componentWillUnmount(): void {
        
        // Metoda używana w momencie usuwania komponentu lub na początku przy odświeżaniu

        console.log("componentWillUnmount - USUWAM KOMPONENT");

    }

    shouldComponentUpdate(nextProps: Props, nextState: State) {
        
        // Zwraca true lub false, decyduje o tym, czy komponent ma ponownie zostać
        // wyrenderowany (wyświetlony). Dzięki niej możemy np. z powodów wydajnościowych
        // zablokować ponowne renderowane. Uruchamiana jest w momencie uzyskania nowych
        // props lub zmiany stanu

        //Tutaj dla przykładu, właczamy aktualizację widoku tylko wtedy, gdy wartość licznika przed zmianą jest liczbą nieparzystą
        //Zatem pokazywane są tylko liczby parzyste (po zmianie)
        
        
        if (this.state.counter % 2 == 0) {
            console.log("   shouldComponentUpdate - BRAK AKTUALIZACJI");
            return false;
        }
        else {

            console.log("   shouldComponentUpdate - Zlecenie AKTUALIZACJI");
            return true;
        }
    }

    //Wyświetlenie lub aktualizacja widoku komponentu
    render() {
        return (
            <div>
                <h3>Stan licznika: {this.state.counter}</h3>
                <button onClick={this.increase}>Zwiększ o jeden</button>
            </div>
        );
    }

}

export default MyComponentLife;
