import React from 'react';
import { Component } from "react";
import { Routes, Route } from 'react-router-dom'; 


import FirstApp from '../firstapp/FirstApp';
import CounterApp from '../counter/CounterApp';
import  MyComponentLife from "../lifecycle/MyComponentLife";
import BookApp from '../books/BookApp';
import DatabaseApp from '../sdatabase/DatabaseApp';
import NameForm from '../testydialog/NameForm';
import DNameForm from '../testydialog/DNameForm';
import TextAreaForm from '../testydialog/TextAreaForm';
import ListForm from '../testydialog/ListForm';
import RadiobuttonForm from '../testydialog/RadiobuttonForm';
import CheckboxForm from '../testydialog/CheckboxForm';
import ManyElemForm from '../testydialog/ManyElemForm';
import ManyElemFormValid from '../testydialog/ManyElemFormValid';
import  MyFormikYup from "../testformik/MyFormikYup";
import  TwoWindows from "../testydialog/TwoWindows";
import  ReadPerson from "../database/ReadPerson";
import  DataBasePerson from "../restbase/DataBasePerson";
import AppStorage from "../teststorage/AppStorage";
import  BankTrans from "../restbank/BankTrans";
import  BPersonDatabase from "../testbootstrap/BPersonDatabase";
import  BootstrapDemo from "../bootstrapdemo/BootstrapDemo";


interface Props {
};

interface State {
}


class MenuGlobRoutes extends Component<Props, State> {

    constructor(props: Props) {
        super(props);
    }

    render() {
        return (
            <Routes>                
                <Route path='/' element={<FirstApp />} />
                <Route path='/CounterApp' element={<CounterApp />} />                
                <Route path='/MyComponentLife' element={<MyComponentLife />} />                

                <Route path='/NameForm' element={<NameForm />} />
                <Route path='/DNameForm' element={<DNameForm />} />
                <Route path='/TextAreaForm' element={<TextAreaForm />} />
                <Route path='/ListForm' element={<ListForm />} />
                <Route path='/RadiobuttonForm' element={<RadiobuttonForm />} />
                <Route path='/CheckboxForm' element={<CheckboxForm />} />
                <Route path='/ManyElemForm' element={<ManyElemForm />} />
                <Route path='/ManyElemFormValid' element={<ManyElemFormValid />} />
                <Route path='/TwoWindows' element={<TwoWindows />} />
                <Route path='/MyFormikYup' element={<MyFormikYup />} />
                <Route path='/BookApp' element={<BookApp />} />
                <Route path='/DatabaseApp' element={<DatabaseApp />} />
                <Route path='/DataBasePerson' element={<DataBasePerson />} />
                <Route path='/AppStorage' element={<AppStorage />} />
                <Route path='/BankTrans' element={<BankTrans />} />
                <Route path='/BPersonDatabase' element={<BPersonDatabase />} />
                <Route path='/BootstrapDemo' element={<BootstrapDemo />} />                
            </Routes>
        );
    }
}


export default MenuGlobRoutes;

